const BalanceGameSelectResult = () => {
  const navigate = useNavigate();
  const { state } = useLocation();
  const [searchParams, setSearchParams] = useSearchParams();
  const [userId, setUserId] = useState("");
  const [jwt, setJwt] = useState("");
  const [rnSend, setRnSend] = useState(false);
  const [isApp, setIsApp] = useState(null);
  const [platform, setPlatform] = useState(null);
  const [bookMarkList1, setBookMarkList1] = useState([]);
  const [bookMarkList2, setBookMarkList2] = useState([]);
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const pathId = searchParams.get("id");
  // const [pathId, setPathId] = useState(null);
  // const [check, setCheck] = useState(state?.check ? state?.check : 0);
  // alert(JSON.stringify(state));

  const getData = async () => {
    setIsLoading(true);
    const reqJwt = state ? state.jwt : jwt;
    await axios({
      method: "get",
      headers: {
        "Access-Control-Allow-Credentials": true,
        "ngrok-skip-browser-warning": true,
        Authorization: reqJwt,
      },
      url: `${process.env.REACT_APP_API_URL}/balancegame/result1?balanceGameId=${
        state ? state.pathId : pathId
      }&${reqJwt ? "" : "guest_name=" + state.guest_name}`,
    })
      .then((res) => {
        // alert(JSON.stringify(res.data));
        setData(res.data.balanceResult);
        let copyData1 = [...res.data.balanceResult.recommendPlace1].map((el) => el.bookMark);
        let copyData2 = [...res.data.balanceResult.recommendPlace2].map((el) => el.bookMark);
        setBookMarkList1(copyData1);
        setBookMarkList2(copyData2);
        setIsLoading(false);
        // alert(`jwt: ${state ? state.jwt : jwt},${JSON.stringify(res.data)}`);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  // const focusedData = async () => {
  //   const reqJwt = state ? state.jwt : jwt;
  //   await axios({
  //     method: "get",
  //     headers: {
  //       "Access-Control-Allow-Credentials": true,
  //       "ngrok-skip-browser-warning": true,
  //       Authorization: reqJwt,
  //     },
  //     url: `${process.env.REACT_APP_API_URL}/balancegame/result?balanceGameId=${
  //       state ? state.pathId : pathId
  //     }`,
  //   })
  //     .then((res) => {
  //       console.log(res.data);
  //       // alert(JSON.stringify(res.data));
  //       alert(JSON.stringify(jwt));
  //       // setData(res.data.balanceResult);
  //       let copyData1 = [...res.data.balanceResult.recommendPlace1].map((el) => el.bookMark);
  //       let copyData2 = [...res.data.balanceResult.recommendPlace2].map((el) => el.bookMark);
  //       setBookMarkList1(copyData1);
  //       setBookMarkList2(copyData2);
  //       // alert(`jwt: ${state ? state.jwt : jwt},${JSON.stringify(res.data)}`);
  //     })
  //     .catch((err) => {
  //       console.log(err);
  //     });
  // };

  useEffect(() => {
    if (!state) {
      window.ReactNativeWebView?.postMessage(JSON.stringify(true));
      //android
      document.addEventListener("message", (e) => {
        try {
          const parseData = JSON.parse(e.data);
          if (parseData.loginData) {
            setUserId(parseData.loginData.id);
            setJwt(parseData.loginData.jwt);
          }
          setRnSend(true);
          setIsApp(parseData.isApp);
          setPlatform(parseData.platform);
          if (parseData?.focused && rnSend) {
            getData();
          }
        } catch (err) {
          console.log(err);
        }
      });
      //ios
      window.addEventListener("message", (e) => {
        try {
          const parseData = JSON.parse(e.data);
          if (parseData.loginData) {
            setUserId(parseData.loginData.id);
            setJwt(parseData.loginData.jwt);
          }
          setRnSend(true);
          setIsApp(parseData.isApp);
          setPlatform(parseData.platform);
          if (parseData?.focused && rnSend) {
            getData();
          }
        } catch (err) {
          console.log(err);
        }
        // alert("jwt없을때");
      });
    }
    // 카카오톡 sdk 추가
    const script = document.createElement("script");
    script.src = "https://developers.kakao.com/sdk/js/kakao.js";
    script.async = true;
    document.body.appendChild(script);
    return () => document.body.removeChild(script);
  }, []);

  useEffect(() => {
    if (rnSend || state) {
      getData();
    }
  }, [rnSend]);

  const moveMorePlace = (idx) => {
    if (state?.isApp || isApp) {
      const sendData = {
        name: "moveMorePlace",
        balanceName: data?.balanceInfo[idx].balanceName,
        balanceNameId: data?.balanceInfo[idx].balanceNameId,
        balanceGameId: state ? state.pathId : pathId,
      };
      window.ReactNativeWebView?.postMessage(JSON.stringify(sendData));
    } else {
      navigate("/balancemoreplace", {
        state: {
          jwt: state ? state.jwt : jwt,
          userId: state ? state.userId : userId,
          isApp: state ? state.isApp : isApp,
          platform: state ? state.platform : platform,
          balanceName: data?.balanceInfo[idx].balanceName,
          balanceNameId: data?.balanceInfo[idx].balanceNameId,
          balanceGameId: state ? state.pathId : pathId,
        },
      });
    }
  };

  const moveBalanceGame = (el) => {
    navigate(`/balancegameselect?id=${el}`);
  };

  const movePlaceDetail = (el) => {
    if (state?.isApp || isApp) {
      const sendData = {
        name: "movePlaceDetail",
        contentId: el.contentid,
        typeId: el.contenttypeid,
        title: el.title,
      };
      window.ReactNativeWebView?.postMessage(JSON.stringify(sendData));
    } else {
      navigate(`/placedetail/${el.contentid}/${el.contenttypeid}`);
    }
  };
  const saveEvent = (event, listNum, idx, contentId) => {
    event.stopPropagation();
    const copyList1 = [...bookMarkList1];
    const copyList2 = [...bookMarkList2];
    if (!(state ? state.isApp : isApp)) {
      alert("여행대로 APP을 이용해주세요.");
    } else {
      if (!(state ? state.jwt : jwt)) {
        alert("로그인이 필요합니다.");
      } else {
        if (listNum === 1) {
          if (
            window.confirm(
              copyList1[idx] ? "장소 저장을 취소하시겠습니까?" : "장소를 저장하시겠습니까?"
            )
          ) {
            reqSave(copyList1, idx, setBookMarkList1, contentId);
            // copyList1[idx] = !copyList1[idx];
            // setBookMarkList1(copyList1);
          } else {
            return;
          }
        } else {
          if (
            window.confirm(
              copyList2[idx] ? "장소 저장을 취소하시겠습니까?" : "장소를 저장하시겠습니까?"
            )
          ) {
            copyList2[idx] = !copyList2[idx];
            setBookMarkList2(copyList2);
          } else {
            return;
          }
        }
      }
    }
  };
  const reqSave = async (list, idx, setList, contentId) => {
    await axios({
      method: "post",
      url: `${process.env.REACT_APP_API_URL}/save`,
      headers: {
        Authorization: state ? state.jwt : jwt,
      },
      data: {
        id: state ? state.userId : userId,
        contentid: contentId,
      },
    })
      .then((res) => {
        if (res.data.boolean) {
          // alert(JSON.stringify(res.data));
          list[idx] = !list[idx];
          setList(list);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const kakaoShareEvent = (event, el) => {
    event.stopPropagation();
    if (state?.isApp || isApp) {
      const sendData = {
        name: "kakaoShare",
        contentId: el.contentid,
        typeId: el.contenttypeid,
        title: el.title,
        image: el.firstimage,
      };
      window.ReactNativeWebView?.postMessage(JSON.stringify(sendData));
    } else {
      if (window.Kakao) {
        const kakao = window.Kakao;

        if (!kakao.isInitialized()) {
          // javascript key
          kakao.init("69c873f1cf4e580ab99c761390d9777e");
        }
        kakao.Link.sendDefault({
          objectType: "feed",
          content: {
            title: `${el.title}`,
            description: "장소정보를 확인해보세요.",
            imageUrl: `${el.firstimage}`,
            imageWidth: 400,
            imageHeight: 300,
            link: {
              mobileWebUrl: `${process.env.REACT_APP_WEB_URL}/placedetail/${el.contentid}`,
              webUrl: `${process.env.REACT_APP_WEB_URL}/placedetail/${el.contentid}`,
            },
          },
          buttons: [
            {
              title: "정보 보기",
              link: {
                // 기본 도메인 설정값으로 가짐 (링크를 뭘 주던) 수정
                mobileWebUrl: `${process.env.REACT_APP_WEB_URL}/placedetail/${el.contentid}`,
                webUrl: `${process.env.REACT_APP_WEB_URL}/placedetail/${el.contentid}`,
              },
            },
          ],
        });
      }
    }
  };

  const checkedBorderEvent = (idx) => {
    if (data?.isMember) {
      return data?.balanceInfo[idx].check
        ? { borderStyle: "solid", borderColor: "#25B1FA", borderWidth: 9 }
        : {};
    } else {
      return state?.check[idx]
        ? { borderStyle: "solid", borderColor: "#25B1FA", borderWidth: 9 }
        : {};
    }
  };
  const checkedIconEvent = (idx) => {
    if (data?.isMember) {
      return { display: data?.balanceInfo[idx].check ? "flex" : "none" };
    } else {
      return { display: state?.check[idx] ? "flex" : "none" };
    }
  };

  const addrReplace = (place) => {
    if (place !== undefined) {
      const splitAddr = place.split(" ");
      const Addr = splitAddr.slice(0, 2).join(" ");
      return Addr;
    }
  };
  return (
    <Container>
      {isLoading ? (
        <LoadingBox>
          <ClipLoader color="#25B1FA" />
        </LoadingBox>
      ) : (
        <>
          <BalanceGameResultContainer>
            <BalanceGameResultTextArea>
              <BalanceGameResultText>{data?.title} 투표 결과</BalanceGameResultText>
            </BalanceGameResultTextArea>
            <BalanceGameResultImageContainer>
              <BalanceGameResultImageArea>
                <BalanceGameResultImage src={data?.balanceInfo[0].balanceImage} />
                <RatioText style={checkedBorderEvent(0)}>{data?.balanceInfo[0].percent}</RatioText>
                <BalanceGameWinnerCheckImage src={SelectCheck} style={checkedIconEvent(0)} />
              </BalanceGameResultImageArea>

              <BalanceGameResultImageArea>
                <BalanceGameResultImage src={data?.balanceInfo[1].balanceImage} />
                <RatioText style={checkedBorderEvent(1)}>{data?.balanceInfo[1].percent}</RatioText>
                <BalanceGameWinnerCheckImage src={SelectCheck} style={checkedIconEvent(1)} />
              </BalanceGameResultImageArea>
            </BalanceGameResultImageContainer>
            <BalanceItemNameContainer>
              <BalanceGameItemNameArea>
                <BalanceGameItemNameText>
                  {data?.balanceInfo[0].balanceName}
                </BalanceGameItemNameText>
              </BalanceGameItemNameArea>
              <BalanceGameItemNameArea>
                <BalanceGameItemNameText>
                  {data?.balanceInfo[1].balanceName}
                </BalanceGameItemNameText>
              </BalanceGameItemNameArea>
            </BalanceItemNameContainer>
            <GrayBorder style={{ marginTop: 18 }} />
          </BalanceGameResultContainer>
          <RecoBalanceGameContainer>
            <RecoBalanceGameTitleNameArea>
              <RecoBalanceGameTitleText>추천 밸런스 게임</RecoBalanceGameTitleText>
            </RecoBalanceGameTitleNameArea>
            <RecoBalanceGameListArea>
              {data?.recommendBalanceGame.map((el) => {
                return (
                  <RecoBalanceGameListContainer
                    key={el.balanceGameId}
                    onClick={() => moveBalanceGame(el.balanceGameId)}
                  >
                    <RecoBalanceGameImageArea>
                      <RecoBalanceGameLeftImage src={el.balanceImage[0]} />
                      <RecoBalanceGameRightImage src={el.balanceImage[1]} />
                    </RecoBalanceGameImageArea>
                    <RecoBalanceGameNameArea>
                      <RecoBalanceGameNameText>{el.title}</RecoBalanceGameNameText>
                    </RecoBalanceGameNameArea>
                  </RecoBalanceGameListContainer>
                );
              })}
            </RecoBalanceGameListArea>
          </RecoBalanceGameContainer>
          <GrayBorder />
          <RecommendPlaceTitleArea>
            <RecommendPlaceTitle>
              {data?.balanceInfo[0].balanceName} 추천해드릴게요
            </RecommendPlaceTitle>
            <RecommendPlaceMore onClick={() => moveMorePlace(0)}>더 보기</RecommendPlaceMore>
          </RecommendPlaceTitleArea>
          <ItemContainer>
            {data?.recommendPlace1.map((el, idx) => {
              return (
                <div key={idx}>
                  <ItemListConatiner onClick={() => movePlaceDetail(el)}>
                    <LeftImageContainer>
                      <ItemImage src={el.firstimage} />
                    </LeftImageContainer>
                    <RightItemContainer>
                      <ItemPlaceName>{el.title}</ItemPlaceName>
                      <ItemPlaceInfoContainer>
                        <ItemInfoText>리뷰 수({el.reviewCount})</ItemInfoText>
                        <ItemInfoText style={{ marginLeft: 4 }}>평점({el.grade / 10})</ItemInfoText>
                      </ItemPlaceInfoContainer>
                      <EnrolledSCContainer>
                        <EnrolledSCImageContainer>
                          <EnrolledSCImage src={el?.scheduleProfile[0]} />
                          <EnrolledSCImage
                            style={{ position: "absolute", left: 10 }}
                            src={el?.scheduleProfile[1]}
                          />
                          <EnrolledSCImage
                            style={{ position: "absolute", left: 20 }}
                            src={el?.scheduleProfile[2]}
                          />
                        </EnrolledSCImageContainer>
                        <EnrolledSCTextContainer>
                          <EnrolledSCPlusText>+</EnrolledSCPlusText>
                          <EnrolledSCText>
                            {el.scheduleCount}명이 일정에 등록했습니다.
                          </EnrolledSCText>
                        </EnrolledSCTextContainer>
                      </EnrolledSCContainer>
                      <BottomItemContainer>
                        <BottomItemAddrContainer>
                          <BottomItemAddrText>{addrReplace(el.addr1)}</BottomItemAddrText>
                        </BottomItemAddrContainer>
                        <BottomItemBtnContainer>
                          <BtnImage
                            src={bookMarkList1[idx] ? saveActiveBtn : saveBtn}
                            onClick={(event) => saveEvent(event, 1, idx, el.contentid)}
                          />
                          <BtnImage
                            src={shareBtn}
                            onClick={(event) => kakaoShareEvent(event, el)}
                          />
                        </BottomItemBtnContainer>
                      </BottomItemContainer>
                    </RightItemContainer>
                  </ItemListConatiner>
                  <ItemListLine />
                </div>
              );
            })}
          </ItemContainer>
          <GrayBorder />
          <RecommendPlaceTitleArea>
            <RecommendPlaceTitle>
              {data?.balanceInfo[1].balanceName} 추천해드릴게요
            </RecommendPlaceTitle>
            <RecommendPlaceMore onClick={() => moveMorePlace(1)}>더 보기</RecommendPlaceMore>
          </RecommendPlaceTitleArea>
          <ItemContainer style={{ marginBottom: 50 }}>
            {data?.recommendPlace2.map((el, idx) => {
              return (
                <div key={idx}>
                  <ItemListConatiner onClick={(event) => movePlaceDetail(el)}>
                    <LeftImageContainer>
                      <ItemImage src={el.firstimage} />
                    </LeftImageContainer>
                    <RightItemContainer>
                      <ItemPlaceName>{el.title}</ItemPlaceName>
                      <ItemPlaceInfoContainer>
                        <ItemInfoText>리뷰 수({el.reviewCount})</ItemInfoText>
                        <ItemInfoText style={{ marginLeft: 4 }}>평점({el.grade / 10})</ItemInfoText>
                      </ItemPlaceInfoContainer>
                      <EnrolledSCContainer>
                        <EnrolledSCImageContainer>
                          <EnrolledSCImage src={el?.scheduleProfile[0]} />
                          <EnrolledSCImage
                            style={{ position: "absolute", left: 10 }}
                            src={el?.scheduleProfile[1]}
                          />
                          <EnrolledSCImage
                            style={{ position: "absolute", left: 20 }}
                            src={el?.scheduleProfile[2]}
                          />
                        </EnrolledSCImageContainer>
                        <EnrolledSCTextContainer>
                          <EnrolledSCPlusText>+</EnrolledSCPlusText>
                          <EnrolledSCText>
                            {el.scheduleCount}명이 일정에 등록했습니다.
                          </EnrolledSCText>
                        </EnrolledSCTextContainer>
                      </EnrolledSCContainer>
                      <BottomItemContainer>
                        <BottomItemAddrContainer>
                          <BottomItemAddrText>{addrReplace(el.addr1)}</BottomItemAddrText>
                        </BottomItemAddrContainer>
                        <BottomItemBtnContainer>
                          <BtnImage
                            src={bookMarkList2[idx] ? saveActiveBtn : saveBtn}
                            onClick={(event) => saveEvent(event, 2, idx, el.contentid)}
                          />
                          <BtnImage
                            src={shareBtn}
                            onClick={(event) => kakaoShareEvent(event, el)}
                          />
                        </BottomItemBtnContainer>
                      </BottomItemContainer>
                    </RightItemContainer>
                  </ItemListConatiner>
                  <ItemListLine />
                </div>
              );
            })}
          </ItemContainer>
        </>
      )}
    </Container>
  );
};

